@import "../variables.scss";

.page-container {
  display: flex;
  flex-direction: column;
  height: calc(110vh); /* works better than 100vh */
  // width: calc(100vw - 15px);
}

.content-wrap {
  flex: 1;
}

.progress-flex-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.progress-flex-item1 {
  flex: 1;
}

.progress-flex-item2 {
  flex: 5;
  max-width: 80%;
  align-self: center;
}

.word-card {
  padding: 0 1vh 1vh 1vh;
}

.survey-link {
  color: $grayish-color;
}

.survey-link:hover {
  color: $primary-color;
}

.audio-control {
  top: 240px;
}

.message-control {
  top: 200px;
}

.blue-button-visibility-control {
  top: 280px;
}

.switch-control {
  position: absolute;
  right: 70px;
}

.input-box {
  margin-top: 0.5vh;
}

